.container {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0; // background: rgba(0, 0, 0, 0.5);
    z-index: 300;
    h1 {
        position: absolute;
        top: 55vh;
        left: 50%;
        transform: translateX(-50%);
        font-size: 10vw;
        white-space: nowrap;
        color: white;
        font: bold;
        opacity: 0;
    }
}

.svg__bg {
    fill: rgba(0, 0, 0, 0.6);
}

.svg__logo {
    transform-origin: center; // transform: translate(-50%);
}

.svg__hintCircle {
    transform-origin: center;
}



