.container {
    position: fixed;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 100;
    transition: transform 0.3s ease-out;
    transform: scale(0);
    background: gray;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden; // prevent broswer reload or pre/next page
    canvas {
        background: white;
        border: 1rem dashed skyblue;
    }
    &:global.active {
        transform: scale(1);
    }
    @media (orientation: landscape) {
        display: flex !important;
        div {
            display: flex;
        }
    }
}

.box__btns {
    display: flex;
    flex-direction: row;
    position: fixed;
    padding: 2vmin;
    bottom: 0;
    right: 0;
}

.btn__signatureDone,
.btn__signatureClean {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 7vmin;
    margin: 0 1vmin;
    color: white;
    width: 15vmin;
    height: 15vmin;
    border-radius: 50%;
    background-color: green;
    cursor: pointer;
}



